.notification {
  display: inline-block;
  width: 320px;
  padding: 15px;
  box-sizing: border-box;
  border-radius: 4px;
  position: fixed;
  right: 25px;
  background-color: #fff;
  box-shadow: 0 0 1px rgba(0,0,0,0.1), 0 2px 4px rgba(0,0,0,0.2);
  -webkit-transition: opacity 0.2s, top 0.2s;
  transition: opacity 0.2s, top 0.2s;
  overflow: hidden;
  z-index: 100;
}
.notification .group {
  float: left;
  display: inline-block;
  width: 230px;
  color: #666;
}
.notification .icon {
  display: inline-block;
  box-sizing: border-box;
  height: 48px;
  line-height: 42px;
  text-align: center;
  width: 48px;
  font-size: 46px;
  float: left;
  margin-right: 10px;
}
.notification .icon.success {
  color: #87d068;
}
.notification .icon.error {
  color: #f60;
}
.notification .icon.info {
  color: #2db7f5;
}
.notification .icon.warning {
  color: #fac450;
}
.notification span {
  font-size: 16px;
}
.notification p {
  font-size: 14px;
  margin: 5px 0 0 0;
}
.notification .closeBtn {
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
}
.fade-leave {
  opacity: 0;
}


