.sidebar {
  background-color: #1c3049;
  min-height: 720px;
  overflow: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.avatar {
  width: 100%;
  padding: 0;
  margin: 0;
  margin-bottom: 30px;
  padding-top: 60px;
}
.avatar img {
  height: 160px;
  width: auto;
  border-radius: 90px;
  border: 10px solid rgba(200,200,200,0.5);
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.avatar figcaption {
  color: #ccc;
  font-size: 22px;
  line-height: 36px;
  text-align: center;
}
.sources {
  padding: 0;
  line-height: 52px;
  width: 100%;
  flex-start: top;
}
.sources .source {
  border-top: 2px solid rgba(200,200,200,0.7);
}
.sources .source:last-child {
  border-bottom: 2px solid rgba(200,200,200,0.7);
}
.sources .source a {
  display: block;
  width: 100%;
  color: #aaa;
  font-weight: bold;
  text-indent: 36px;
}
.sources .source a:hover {
  color: #ccc;
  background-color: #101b28;
  cursor: pointer;
}
.github-container {
  width: 240px;
  position: absolute;
  bottom: 30px;
}
.github-container .github {
  margin-left: auto;
  margin-right: auto;
}


